.Navbar{
    padding: 0;
    .container-fluid{
        padding: 0 40px;
        @media (max-width: 445px){
            padding: 0 20px;
        }
        @media (max-width: 740px){
            padding: 0 20px;
        }
        .navbar{
            h3{
                font-size: 38px;
                color: white;
                font-family: 'Yellowtail', cursive;
                @media (max-width: 570px){
                    font-size: 30px;
                }
                @media (max-width: 445px){
                    font-size: 25px;
                }
                span{
                    color: dodgerblue;
                }
            }
            .navbar-nav{
                @media (max-width: 840px){
                    z-index: 1000000001;
                    display: block;
                    position: fixed;
                    top: 0px;
                    right: -50%;
                    width: 50%;
                    height: 100vh;
                    background-color: rgb(252, 35, 79);
                    padding-top: 40px;
                    line-height: 60px;
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                }
                .delete-nav{
                    color: white;
                    font-size: 40px;
                    font-weight: 600;
                    font-family: 'Nunito', sans-serif;
                    margin-left: 75%;
                    margin-top: -50%;
                    background-color: transparent;
                    outline: none;
                    border: none;
                }
                li{
                    padding: 0 20px;
                    @media (max-width: 840px){
                        padding: 2% 27% 2% 5%;     
                    }
                    a{
                        color: white;
                        font-size: 20px;
                        font-family: 'Nunito', sans-serif;
                        transition-duration: 0.3s;
                        border-radius: 5px;
                        -webkit-border-radius: 5px;
                        -moz-border-radius: 5px;
                        -ms-border-radius: 5px;
                        -o-border-radius: 5px;
                        .icon{
                            display: none;
                            @media (max-width: 840px){
                                display: inline-block;
                            }
                        }
                        &:hover{
                            background-color: white;
                            color: rgb(252, 35, 79);
                            font-weight: 600;
                        }
                        &:focus{
                            color: dodgerblue;
                            font-size: 20px;
                            font-weight: 600;
                            font-family: 'Nunito', sans-serif;
                        }
                        @media (max-width: 840px){
                            padding: 0 10%;
                        }
                    }
                }
            }
            .lang{
                padding: 10px;
                outline: none;
                background-color: white;
                cursor: pointer;
                font-family: 'Nunito', sans-serif;
                color: rgb(252, 35, 79);
                font-weight: 600;
                border: none;
                border-radius: 5px;
                -webkit-border-radius: 5px;
                -moz-border-radius: 5px;
                -ms-border-radius: 5px;
                -o-border-radius: 5px;
            }
            .button-navbar{
                display: none;
                color: white;
                font-size: 20px;
                font-weight: 600;
                @media (max-width: 840px){
                    display: inline-block;
                }
            }
        }
    }
}

.crimson{
    background-color: rgb(252, 35, 79);
    transition-duration: 0.5s;
}
.saw{
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
    -moz-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
    -o-transform: translateX(-100%);
}
.black{
    background-color: rgba(0, 0, 0, 0.726);
    z-index: 100000000;
    width: 100%;
    height: 100vh;
    position: fixed;
    margin-top: -72px;
}