.Contacts{
    padding: 0;
    margin: 0;
    background: url(../images/abstract.jpg) no-repeat;
    background-size: 100% 100%;
    .container-fluid{
        padding: 20px;
        background-color: rgba(0, 0, 0, 0.651);
        .col-md-12{
            h1{
                color: white;
                font-family: 'Nunito', sans-serif;
                font-size: 50px;
                font-weight: 600;
                padding: 20px;
            }
        }
        .row{
            padding: 30px 30px;
            .contact-tools{
                .shadow{
                    border-radius: 10px;
                    background-color: white;
                    padding: 14px;
                    .d-flex-body{
                        display: flex;
                        justify-content: space-between;
                        @media (max-width: 495px){
                            display: block;
                        }
                        .col-md-6{
                            .col-12{
                                padding: 30px 20px;
                                @media (max-width: 767px){
                                    padding: 20px;
                                }
                            }
                        }
                    }

                }
                .tools{
                    display: flex;
                    align-items: center;
                    i{
                        font-size: 30px;
                        color: black;
                        @media (max-width: 1110px){
                            font-size: 25px;
                        }
                        @media (max-width: 767px){
                            font-size: 30px;
                        }
                        @media (max-width: 620px){
                            font-size: 25px;
                        }
                        @media (max-width: 550px){
                            font-size: 20px;
                        }
                        @media (max-width: 495px){
                            font-size: 28px;
                        }
                        @media (max-width: 400px){
                            font-size: 24px;
                        }
                        @media (max-width: 350px){
                            font-size: 22px;
                        }
                    }
                    span{
                        color: black;
                        font-size: 15px;
                        @media (max-width: 1110px){
                            font-size: 13px;
                        }
                        @media (max-width: 767px){
                            font-size: 15px;
                        }
                        @media (max-width: 620px){
                            font-size: 13px;
                        }
                        @media (max-width: 550px){
                            font-size: 11px;
                        }
                        @media (max-width: 495px){
                            font-size: 14px;
                        }
                        @media (max-width: 400px){
                            font-size: 12px;
                        }
                        @media (max-width: 350px){
                            font-size: 10px;
                        }
                        h4{
                            font-size: 20px;
                            color: black;
                            @media (max-width: 1069px){
                                font-size: 18px;
                            }
                            @media (max-width: 767px){
                                font-size: 20px;
                            }
                            @media (max-width: 620px){
                                font-size: 18px;
                            }
                            @media (max-width: 550px){
                                font-size: 15px;
                            }
                            @media (max-width: 495px){
                                font-size: 20px;
                            }
                            @media (max-width: 400px){
                                font-size: 18px;
                            }
                            @media (max-width: 350px){
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
            .contact-form{
                padding: 40px;
                background-color: white;
                border-radius: 10px;
                font-size: 20px;
                font-weight: 600;
                font-family: 'Nunito', sans-serif;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                -ms-border-radius: 10px;
                -o-border-radius: 10px;
                .name, .email, .text{
                    padding: 13px;
                    font-size: 18px;
                }
                label{
                    @media (max-width: 767px){
                        margin-top: 5%;
                    }
                }
                .btn{
                    background-color: dodgerblue;
                    color: white;
                    font-size: 20px;
                    font-weight: 600;
                    padding: 13px;
                    @media (max-width: 370px){
                        font-size: 18px;
                        font-weight: 500;
                    }
                    @media (max-width: 345px){
                        font-size: 15px;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}