.Work{
    padding: 0;
    margin: 0;
    background: url(../images/work.png) no-repeat;
    background-size: 100% 100%;
    .container-fluid{
        background-color: rgba(0, 0, 0, 0.651);
        padding: 30px;
        @media (max-width: 767px){
            padding: 30px 90px;
        }
        @media (max-width: 560px){
            padding: 30px 60px;
        }
        @media (max-width: 470px){
            padding: 30px 30px;
        }
        @media (max-width: 390px){
            padding: 30px 10px;
        }
        .work-text{
            h1{
                color: white;
                font-size: 55px;
                font-family: 'Nunito', sans-serif;
                @media (max-width: 560px){
                    font-size: 45px;
                }
            }
        }
        .row{
            .col-md-4{
                height: 35vh;
                padding: 0 50px;
                @media (max-width: 767px){
                    padding: 0 20px;
                }
                // border: solid 1px white;
                .mikekus{
                    width: 100%;
                    height: 100%;
                    position: relative;
                    a{
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        background: url(../images/lantern.jpg) no-repeat;
                        background-size: cover;
                        border-radius: 8px;
                        -webkit-border-radius: 8px;
                        -moz-border-radius: 8px;
                        -ms-border-radius: 8px;
                        -o-border-radius: 8px;
                        .contrast{
                            background-color: rgba(0, 0, 0, 0.541);
                            opacity: 0;
                            width: 100%;
                            height: 100%;
                            border-radius: 8px;
                            -webkit-border-radius: 8px;
                            -moz-border-radius: 8px;
                            -ms-border-radius: 8px;
                            -o-border-radius: 8px;
                            transition-duration: 0.3s;
                            color: white;
                            font-size: 0px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            transition:0.5s;
                            -webkit-transition:0.5s;
                            -moz-transition:0.5s;
                            -ms-transition:0.5s;
                            -o-transition:0.5s;
                            &:hover{
                                opacity: 1;
                                font-size: 25px;
                            }
                        }
                    }
                    .wild{
                        background: url(../images/wild.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                    .clever{
                        background: url(../images/clever.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                    .everything{
                        background: url(../images/everything.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                    .online-shop{
                        background: url(../images/onlineshop.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                    .meneg-serv{
                        background: url(../images/menejer.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                    .honey-moon{
                        background: url(../images/honeymoon.PNG) no-repeat;
                        background-size: 100% 100%;
                    }
                }
            }
        }
    }
}