.Skills{
    padding: 0;
    margin: 0;
    .container-fluid{
        padding: 30px;
        .col-md-12{
            h1{
                font-size: 55px;
                font-family: 'Nunito', sans-serif;
            }
        }
        .row{
            .skill-text{
                padding: 40px 40px;
                @media (max-width: 540px){
                    padding: 40px 10px;
                }
                h2{
                    font-size: 27px;
                    @media (max-width: 767px){
                        font-size: 24px;
                    }
                }
                p{
                    font-weight: 600;
                    font-size: 18px;
                    text-align: justify;
                    @media (max-width: 767px){
                        font-size: 16px;
                    }
                }
            }
            .col-md-7{
                padding: 40px 40px;
                @media (max-width: 540px){
                    padding: 40px 10px;
                }
                .head{
                    .bar{
                        height: 100%;
                        display: flex;
                        align-items: center;
                        background-color: dodgerblue;
                        .body{
                            background-color: transparent;
                            color: white;
                            font-size: 15px;
                        }
                    }
                    .c1{
                        width: 50%;
                    }
                    .c2{
                        width: 70%;
                    }
                    .c3{
                        width: 70%;
                    }
                    .c4{
                        width: 75%;
                    }
                    .c5{
                        width: 65%;
                    }
                    .c6{
                        width: 70%;
                    }
                }
            }
        }
    }
}