.Home{
    margin: 0;
    padding: 0;
    background: url(../images/abstract3.jpg) no-repeat;
    // height: 100vh;
    background-size: 100% 100%;
    .row{
        padding: 73px 0 0 0;
        margin: 0;
        .identify-img{
            @media (min-width: 767.1px){
                display: none;
            }
            @media (min-width: 510px){
                padding: 30px 150px;
            }
            @media (min-width: 445px){
                padding: 30px 100px;
            }
            padding: 30px 50px;
            .circle-identify{
                background-color: black;
                border: solid 15px crimson;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                img{
                    width: 78%;
                    height: 70%;
                    filter: opacity(0.9) contrast(1.7);
                    -webkit-filter: opacity(0.9) contrast(1.7);
                }
            }
        }
        .identify{
            margin: 0;
            padding: 0;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            // border: solid 1px white;
            --bs-gutter-x: 0;
            @media (max-width: 916px){
                padding: 0;
                margin: 0;
            }
            @media (max-width: 767px){
                display: none;
            }
            img{
                filter: opacity(0.9) contrast(1.7);
                -webkit-filter: opacity(0.9) contrast(1.7);
                @media (max-width: 951px){
                    width: 70%;
                    height: 100%;
                }
            }
        }
        .home-text{
            --bs-gutter-x: 0;
            margin: 0;
            padding: 120px 40px 120px 100px;
            @media (max-width: 767px){
                padding: 20px 40px 40px 40px;
                text-align: center;
            }
            h1{
                color: dodgerblue;
                font-family: 'Crimson', cursive;
                font-size: 60px;
                @media (max-width: 1286px){
                    font-size: 40px;
                }
                @media (max-width: 951px){
                    font-size: 30px;
                }
            }
            h3{
                color: white;
                font-family: 'Crimson', cursive;
                font-size: 40px;
                @media (max-width: 1286px){
                    font-size: 27px;
                }
                @media (max-width: 951px){
                    font-size: 22px;
                }
            }
            p{
                color: white;
                font-size: 20px;
                margin-bottom: 40px;
                font-family: 'Nunito', sans-serif;
                text-align: left;
                @media (max-width: 1286px){
                    font-size: 15px;
                }
                @media (max-width: 767px){
                    text-align: center;
                }
            }
            .hire{
                text-decoration: none;
                font-size: 32px;
                font-weight: 600;
                font-family: 'Nunito', sans-serif;
                color: white;
                background-color: dodgerblue;
                padding: 10px 60px;
                border-radius: 15px;
                &:hover{
                    background-color: transparent;
                    border: solid 2px white;
                }
                @media (max-width: 951px){
                    font-size: 30px;
                }
                @media (max-width: 367px){
                    font-size: 20px;
                    padding: 10px 30px;
                }
            }
            .home-contact{
                display: flex;
                @media (max-width: 767px){
                    justify-content: center;
                }
                .icon{
                    font-size: 50px;
                    color: white;
                    @media (max-width: 600px){
                        font-size: 35px;
                    }
                    &:hover{
                        color: dodgerblue;
                    }
                }
                .instagram, .telegram{
                    margin-left: 25px;
                }
            }
        }
    }   
    .fixed-bottom{
        left: auto;
        padding: 0 20px 0 30px;
        z-index: 100001;
        color: crimson;
        text-decoration: none;
        font-size: 70px;
    }
    .scroll-up{
        position: fixed;
        z-index: 100001;
        color: crimson;
        right: auto;
        left: 2%;
        top: 110%;
        background-color: white;
        font-size: 60px;
        line-height: 45px;
        width: 50px;
        height: 45px;
        border-radius: 50px;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        -ms-border-radius: 50px;
        -o-border-radius: 50px;
        transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        -ms-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        i{
            width: 115%;
            margin-right: 20px;
            margin-left: -5px;
        }
    }
    .show{
        top: 88%;
    }
}