.About{
    margin: 0;
    padding: 20px 0 40px 0;
    // height: 100vh;
    .container-fluid{
        width: 100%;
        height: 100%;
        h1{
            color: black;
            font-family: 'Nunito', sans-serif;
            font-size: 50px;
            font-weight: 600;
            padding: 20px;
        }
        .row{
            .img{
                padding: 10px 110px;
                @media (max-width: 767px){
                    display: flex;
                    justify-content: center;
                    padding: 10px 30px;
                }
                .about-img{
                    width: 100%;
                    height: 90%;
                    display: flex;
                    align-items: center;
                    overflow: hidden;
                    background: url(../images/background2.jpg) no-repeat;
                    background-size: 100% 100%;
                    border-radius: 15px;
                    -webkit-border-radius: 15px;
                    -moz-border-radius: 15px;
                    -ms-border-radius: 15px;
                    -o-border-radius: 15px;
                    @media (max-width: 1115px){
                        margin-top: 20%;
                    }
                    @media (max-width: 1035px){
                        height: 80%;
                        margin-top: 30%;
                    }
                    @media (max-width: 995px){
                        width: 120%;
                        margin-top: 35%;
                    }
                    @media (max-width: 930px){
                        // width: 120%;
                        height: 65%;
                        margin-top: 55%;
                    }
                    @media (max-width: 767px){
                        width: 120%;
                        margin-top: 0%;
                        height: 100%;
                    }
                    .back-img{
                        background-color: rgba(0, 0, 0, 0.788);
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        border-radius: 15px;
                        -webkit-border-radius: 15px;
                        -moz-border-radius: 15px;
                        -ms-border-radius: 15px;
                        -o-border-radius: 15px;
                        animation: glitch1 10s infinite  linear;
                        -webkit-animation: glitch1 10s infinite  linear;
                        @keyframes glitch1{
                            0%{
                                background-color: rgba(0, 0, 0, 0.788);
                            }
                            25%{
                                background-color: rgba(0, 0, 0, 0);
                            }
                            50%{
                                background-color: rgba(0, 0, 0, 0.788);
                            }
                            75%{
                                background-color: rgba(0, 0, 0, 0);
                            }
                            100%{
                                background-color: rgba(0, 0, 0, 0.788);
                            }
                        }
                        img{
                            position: relative;
                            height: 100%;
                            filter: opacity(0.7) grayscale(1) contrast(1.3);
                            -webkit-filter: opacity(0.7) grayscale(1) contrast(1.3);
                            animation: glitch 10s infinite  linear;
                            -webkit-animation: glitch 10s infinite  linear;
                            @media (max-width: 1260px){
                                margin-left: -20%;
                            }
                            @media (max-width: 1115px){
                                width: 90%;
                                margin-left: -5%;
                            }
                            @keyframes glitch{
                                0%{
                                    filter: opacity(0.7) grayscale(1) contrast(1.3);
                                    -webkit-filter: opacity(0.7) grayscale(1) contrast(1.3);
                                }
                                25%{
                                    filter: opacity(1) grayscale(0) contrast(1.3);
                                    -webkit-filter: opacity(1) grayscale(0) contrast(1.3);
                                }
                                50%{
                                    filter: opacity(0.7) grayscale(1) contrast(1.3);
                                    -webkit-filter: opacity(0.7) grayscale(1) contrast(1.3);
                                }
                                75%{
                                    filter: opacity(1) grayscale(0) contrast(1.3);
                                    -webkit-filter: opacity(1) grayscale(0) contrast(1.3);
                                }
                                100%{
                                    filter: opacity(0.7) grayscale(1) contrast(1.3);
                                    -webkit-filter: opacity(0.7) grayscale(1) contrast(1.3);
                                }
                            }
                        }
                    }
                }
            }
            .text1{
                padding: 70px 100px 10px 50px;
                @media (max-width: 767px){
                    padding: 20px 30px;
                }
                .about-text{
                    h2{
                        font-family: 'Nunito', sans-serif;
                        font-weight: 600;
                        font-size: 27px;
                        @media (max-width: 767px){
                            font-size: 24px;
                        }
                        span{
                            color: dodgerblue;
                        }
                    }
                    p{
                        font-family: 'Nunito', sans-serif;
                        text-align: justify;
                        font-size: 18px;
                        font-weight: 600;
                        @media (max-width: 767px){
                            font-size: 16px;
                        }
                    }
                    a{
                        font-family: 'Nunito', sans-serif;
                        font-weight: 500;
                        background-color: dodgerblue;
                        font-size: 25px;
                        color: white;
                        .icon{
                            font-size: 35px;
                            color: white;
                        }
                    }
                }
            }
        }
    }
}